.searchField {
  padding: 20px 5px;
  display: flex;
  justify-content: space-around;
  background: #2c3e50;
}

#search-field input {
  width: 40px;
  padding: 5px;
}

input#city,
input#country,
button#get {
  width: 60px;
  font-size: 10px;
  padding: 3px 5px;
}

input:-webkit-autofill {
  -webkit-box-shadow: 0 0 0 30px #2c3e50 inset;
  -webkit-text-fill-color: #fff !important;
}

input[type="text"]:focus {
  -webkit-box-shadow: 0 0 0 30px #2c3e50 inset;
  -webkit-text-fill-color: #fff !important;
}

button#get {
  border: 0;
  margin: 0 8px 0 0;
  border-radius: 2px;
  font-weight: lighter;
  letter-spacing: 1px;
  cursor: pointer;
  background-color: #f16051;
  color: #fff;
  font-weight: 100;
}

